    .container{
        padding: 0 .3rem !important;
    }
    .content{
        padding: 0 !important;
        border: none !important;
    }
    .surprised-top{
        flex-direction: column;
        align-items: flex-start;
        .sale{
            margin-top: .2rem;
        }
    }
}
.border-box{
    border: 1px solid #545559;
    border-radius: .1rem;
    padding: .2rem .3rem;
    background: linear-gradient(0deg, #141930, #101529);
    margin-bottom: .3rem;
}
