        padding: .3rem .2rem;
        border: 1px solid #545559;
        border-radius: .14rem;
    }
    .surprised{
        
        .surprised-avatar{
            width: .53rem;
            height: .53rem;
        }
        .sale{
            height: .36rem;
            line-height: .36rem;
            padding: 0 .15rem;
            background: rgba(244, 168, 1, 0.11);
            border-radius: .2rem;
            color: #F4A801;
        }
        .surprised-logo{
            width: .18rem;
            height: auto;
            margin-right: .14rem;
        }
    }
    .sale-list__item{
        border-bottom: 1px solid #545559;
        padding: .18rem 0;
        font-size: .16rem;
        &:last-child{
            border-bottom: none;
        }
        .sale-list__desc{
            color: #FBC200;
        }
    }
    .sale-list__content{
        align-items: flex-start;
    }
    .sale-list{
        padding: 0 .3rem;
    }
    .progress-col{
        margin: 0 auto;
    }
    .progress-title{
        padding: .1rem .2rem;
        border: 1px solid #F5BD02;
        border-radius: .04rem;
        color: #F5BD02;
        
    }
    .progress-text__active{
        color: #F5BD02;
    }
    .progress-input{
        width: 2.9rem;
        height: .4rem;
        border: 1px solid #FFFFFF;
        border-radius: 6px;
        background: none;
        padding: 0 .2rem;
        >input{
            background: none;
        }
        .progress-input__max{
            color: #F5B802;
        }
    }
    .progress-buy{
        width: .6rem;
        height: .4rem;
        line-height: .4rem;
        background: linear-gradient(-75deg, #F5BD02, #F29401);
        border-radius: .06rem;
    }
    .progress-time{
        .progress-time__item{
            width: .5rem;
            height: .5rem;
            line-height: .5rem;
            border-radius: .08rem;
            background: #fff;
        }
    }
}
</style>